document.addEventListener("DOMContentLoaded", () => {
  const emps = document.querySelectorAll('.employees__user');
  const choise = document.querySelector('.employees__choise-body');
  const table = document.querySelector('.employees__table-body');

  emps?.forEach(item => {
    item.addEventListener('click', () => {
      emps.forEach(item => {
        item.classList.remove('active');
      });

      item.classList.add('active');
      choise.classList.add('visually-hidden');
      table.classList.remove('visually-hidden');
    })
  })
});
